.home .slider {
  position: relative;
  width: 100%;
  height: 350px;
}

@media (min-width: 640px) {
  .home .slider {
    height: 800px;
  }
}

.home .slider [class*='slide-'] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff no-repeat center center;
  background-size: cover;
  z-index: 0;
  transition: opacity .4s linear;
  transition: z-index 0 linear 0.4s;
  opacity: 0;
}

.home .slider [class*='slide-'].active {
  opacity: 1;
  z-index: 1;
}

.home .slider [class*='slide-'] .mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
}

.home .slider [class*='slide-'] .content-of-slide {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
}

.home .slider [class*='slide-'] .content-of-slide h2 {
  margin: 0;
  font-size: 2em;
  text-transform: uppercase;
  color: #ffffff;
}

.home .slider [class*='slide-'] .content-of-slide p {
  margin: 5px 0;
  font-size: 1.2em;
  font-weight: 300;
  color: #ffffff;
}

.home .slider [class*='slide-'] .content-of-slide .read-more {
  display: none;
}

.home .slider [class*='slide-'] .content-of-slide .button {
  display: inline-block;
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ffffff;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
}

.home .slider [class*='slide-'] .content-of-slide .button:hover {
  background: #ffffff;
  color: #000;
}

.home .slider .progress-of-slides {
  position: absolute;
  bottom: 20px;
  left: 0;
  z-index: 1;
  width: 100%;
}

.home .slider .progress-of-slides .progress-btn {
  font-size: 13px;
  text-transform: uppercase;
  width: 10px;
  height: 10px;
  background: #ffffff;
  color: #ffffff;
  float: left;
  border-radius: 5px;
  margin: 0 5px;
  overflow: hidden;
  cursor: pointer;
}

.home .slider .progress-of-slides .progress-btn:first-child {
  margin-left: 0;
}

.home .slider .progress-of-slides .progress-btn.active {
  background: #ffffff;
  color: #ffffff;
  border-color: #ffffff;
}

@media (min-width: 640px) {
  .home .slider .progress-of-slides .progress-btn {
    width: 25%;
    padding: 10px 0;
    border-top: 1px solid #ffffff;
    transition: border-color .4s linear, color .4s linear;
    height: auto;
    background: transparent;
    border-radius: 0;
    margin: 0;
  }
  .home .slider .progress-of-slides .progress-btn.active {
    background: transparent;
  }
}
